* {
    padding: 0;
    margin: 0;
  }
 
  body {
    font-family: sans-serif;
    background-image:
    -webkit-gradient(
      linear, 
      left top, right top, 
      from(red),
      color-stop(rgb(255, 255, 0)), 
      to(green)
    );
    background-image:
    -webkit-linear-gradient(
      left, 
      red,
      rgb(255, 255, 0), 
      green
    );
    background-image:
    linear-gradient(
      to right, 
      red,
      rgb(255, 255, 0), 
      green
    );
  }
 
  a,
  a:visited {
    outline: none;
  }
 
  a:hover {
    text-decoration: none;
    cursor: pointer;
  }
 
  .header {
    text-align: center;
  }
 
  .nav {
    margin: 20px auto;
    max-width: 48em;
    border: 1px solid #333;

    border-left: none;
    border-right: none;
    text-align: center;
  }
 
  .menu {
    display: inline-block;
    padding: 20px;
    text-align: center;
    text-decoration: none;
    color: #333;
    -webkit-transition: background .15s ease-in;
    transition: background .15s ease-in;
  }
 
  .menu:hover {
    background: #ddd;
  }
 
  .menu.is-active {
    background: #ddd;
  }

  .Merah.is-active{
    background: red;
  }

 .Kuning.is-active{
    background: yellow;
  }
 
  .Hijau.is-active{
    background: green;
  }
 
  .Pink.is-active{
    background: palevioletred;
  }

  .Coklat.is-active{
    background: peru;
  }

  .Biru.is-active{
    background: aqua;
  }
 
  .Pink.selected{
    background: palevioletred;
  }

  .Coklat.selected{
    background: peru;
  }

  .Merah.selected{
    background: red;
  }

 .Kuning.selected{
    background: yellow;
  }
 
  .Hijau.selected{
    background: green;
  }
 
  .Biru.selected{
    background: aqua;
  }

  .info {
    margin-top: 40px;
    text-align: center;
  }
 
  .selected {
    margin-left: 5px;
    padding: 5px 10px;
    vertical-align: middle;
    border-radius: 4px;
    background: #ddd;
    text-transform: capitalize;
    font-weight: bold;
    font-size: 1.5em;
  }

  .button {
    background-color: peru; 
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    -webkit-transition-duration: 0.4s; /* Safari */
    transition-duration: 0.4s;
  }

  .button2:hover {
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19);
  }
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

